.modalBackground{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    z-index: 30;

    background: url('../../assets/Backgrounds/bg2.jpg');
        background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
    /* background-color: rgba(0, 0, 0, 0.85); */
    animation: appear 1s;
    animation-direction: alternate;
}

.modalCard{
    position: fixed;
    top: 20%;
    left:25%;
    width: 50vw;
    min-height: 60vh;
    z-index: 40;
    /* background-color: rgb(232, 232, 232); */
    color:white;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 10px;
    border-radius: 5px;

    animation: appear 1s;
    animation-direction: alternate;
}

.website{
    font-size: 20px;
    padding-inline: 15px;
    padding-block: 8px;
    border: 1px solid rgba(0, 140, 2, 0.527); 
    border-radius: 3px;
    text-decoration: none;
    color:white;
    background-color: #e8be01;
}

.website:hover{
}

@keyframes appear{
    from{
        opacity: 0;
    }

    to{
        opacity: 1;
    }
}

.close{
    align-self: flex-start;
    width: 100%;
    text-align: right;
    cursor: pointer;
}

.eventHeading{
    padding-block: 5px;
}
.title{
    padding-block:5px;
}

.description{
    text-align: justify;
    font-size: 20px;
    padding: 15px; 
}

@media (max-width: 1000px){
    .modalCard{
        width: 90%;
        left : 2.3%;
        margin-inline: auto;
        top: 13%;
        min-height: 80vh;
    }
}